*{padding: 0; margin: 0; border: 0;}

*,*:before,*:after{
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
  height: 100%;
  width: 100%;
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
html{scroll-behavior: smooth;}
input,button,textarea{font-family: inherit; font-size: inherit; color: inherit; background: none;}

input::-ms-clear{display: none;}
button{cursor: pointer; background: none;}
button::-moz-focus-inner {padding: 0; border: 0;}
a, a:hover, a:visited{text-decoration: none;}
ul, menu{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit; font-weight: 400;}

body{
  background: var(--color-bg-main);
  color: var(--color-black);
  font-family: 'Roboto', sans-serif;
}

/* Переменные */
:root{
  --color-white: #FFFFFF;
  --color-black: #3E4554;
  --color-blue: #81A0DE;
  --color-blue-hover: #688FDE;
  --color-blue-disabled: #B1BBD0;
  --color-gray: #E9ECF2;
  --color-red: #9F2D2D;
  --color-bg-main: #E9ECF2;
  --color-bg-opacity: rgba(62, 69, 84, 0.27);
  --color-border-input: #99A5BD;
}